import React from "react";
import { RenderRichContent } from "./RenderRichContent";
import Img, { FluidObject } from "gatsby-image";
import "./ImageBlock.scss";
import { ContentfulRichTextGatsbyReference, RenderRichTextData } from "gatsby-source-contentful/rich-text";
type ImageBlockProps = {
  align?: "left" | "right";
  className?: string;
  data: {
    title: string;
    image: { fluid: FluidObject };
    description: RenderRichTextData<ContentfulRichTextGatsbyReference>;
    subHeading?: {
      title: string;
      image?: { fluid: FluidObject }
    };
    button?: {
      title: string;
      link: string;
    };

  };
};
// Height of feature image should be 400px * mandatory => Use graphql for img height ?h=400
export const ImageBlock = ({ align, data, className }: ImageBlockProps) => {

  return (
    <div className={'image-block-wrapper ' + className} img-align={align}>
      <Img
        className="image"
        fluid={data.image.fluid}
        loading="lazy"
        alt="content"
      />
      <div className="text-block">
        <div className="text-block-wrapper">
          <div className="title">{data.title}</div>
          {data.subHeading?.title ? (
            <>
              {data.subHeading?.image ?
                <Img
                  className="image-icon"
                  fluid={data.subHeading?.image?.fluid}
                  loading="lazy"
                  alt="heading icon"
                />
                :
                <></>}
              &nbsp;
              <div className="sub-title">{data.subHeading.title}</div>
            </>
          ) : (
            <></>
          )}
          <div className="description"><RenderRichContent content={data.description} /></div>
          {data.button ? (
            <a href={data.button.link}><button className="button">{data.button.title}</button></a>
          ) : (
            <></>
          )}
        </div>
      </div>
    </div>
  );
};
